import { useState, useEffect } from "react";

import { projectFirestore } from "../../firebase/config";

function TableRow({ word, meaning, sentence, id }) {
  const [currentWord, setCurrentWord] = useState("");
  const [words, setWords] = useState([]);

  useEffect(() => {
    const unsub = projectFirestore
      .collection("dictionary")
      .onSnapshot((snapshot) => {
        if (snapshot.empty) {
          //
        } else {
          let results = [];
          snapshot.docs.forEach((doc) => {
            // console.log(doc);
            results.push({ id: doc.id, ...doc.data() });
          });
          setWords(results);
          // console.log(results);
        }
      });

    return () => unsub();
  }, []);

  const Read = async (id) => {
    setCurrentWord(words.filter((word) => word.id === id).pop());
    console.log(`Word : ${currentWord.word}
                  Meaning: ${currentWord.meaning}
                  Sentence : ${currentWord.sentence}`);
  };
  const DeleteWord = async (id) => {
    projectFirestore.collection("dictionary").doc(id).delete();
  };
  return (
    <tr>
      <td>{word}</td>
      <td>{meaning}</td>
      <td>{sentence}</td>

      <td>
        <button onClick={() => Read(id)}>read</button>
        <button onClick={() => DeleteWord(id)}>delete</button>
      </td>
    </tr>
  );
}
export default TableRow;
