import React from "react";
import { useState } from "react";

function Name() {
  const [showEvents, setShowEvents] = useState(true);
  const [list, changeList] = useState([
    { title: "Need to do laundy!", id: 1 },
    {
      title: "need to go shopping!",
      id: 2,
    },
    { title: "need to go outside", id: 3 },
  ]);
  const handleChange = (id) => {
    changeList((prevState) => {
      return prevState.filter((item) => {
        return item.id !== id;
      });
    });
  };
  console.log(showEvents);
  return (
    <div className="App">
      <div>
        {showEvents && (
          <button
            onClick={() => {
              setShowEvents(false);
            }}
          >
            hide Events
          </button>
        )}
        {!showEvents && (
          <button
            onClick={() => {
              setShowEvents(true);
            }}
          >
            show Events
          </button>
        )}
      </div>
      {showEvents &&
        list.map((e) => (
          <div key={e.id}>
            <h1>{e.title}</h1>
            <button onClick={() => handleChange(e.id)}>Delete</button>
          </div>
        ))}
    </div>
  );
}
export default Name;
