import React from "react";
import { useState } from "react";

function MyTodoList() {
  const [showList, setShowList] = useState(true);
  const [list, changeList] = useState([
    { task: "Todo shopping", id: 1 },
    { task: "to do laundry", id: 2 },
    {
      task: "To feed dog",
      id: 3,
    },
  ]);
  const handleDelete = (selectedItemId) => {
    changeList((prevState) => {
      return prevState.filter((item) => {
        return item.id !== selectedItemId;
      });
    });
  };

  return (
    <>
      <div>
        <h1>This is a to do list</h1>
        <div>
          {showList && (
            <button
              onClick={() => {
                setShowList(false);
              }}
            >
              Hide
            </button>
          )}
          {!showList && (
            <button
              onClick={() => {
                setShowList(true);
              }}
            >
              Show
            </button>
          )}
        </div>
      </div>
      <div>
        {showList && (
          <ul>
            {list.map((item) => (
              <div key={item.id}>
                <li>{item.task}</li>
                <button onClick={() => handleDelete(item.id)}>Delete</button>
              </div>
            ))}
          </ul>
        )}
      </div>
    </>
  );
}

export default MyTodoList;
