import "./App.css";
import logo from "./assets/nodelogo.jpg";
import React, { useState } from "react";
import Welcome from "./components/welcome";
import Message from "./components/message.js";
import Namelist from "./components/namelist.js";
import Form from "./components/form.js";
import Name from "./components/something";
import Title from "./components/Title.js";
import Home from "./pages/home/Home.js";

import MyTodoList from "./components/todolist";
import Modal from "./components/Modal";
import TripList from "./components/TripList";
//creating our first components//
function Header() {
  const [name, changeName] = useState("mush");
  const [dick, changeDick] = useState("kitchen");
  return (
    <header className="App-header">
      <h1>
        {name}'s {dick}
      </h1>
      <button
        onClick={() => {
          changeName("meher");
          changeDick("parlour");
        }}
      >
        changeName
      </button>
    </header>
  );
}
function Main(props) {
  return (
    <section className="App-header">
      <p>
        We serve the most <strong>{props.adjective} </strong> Food!
      </p>
      <img src={logo} alt="a node js logo" height="200"></img>
      <ul style={({ textAlign: "left" }, { color: "red" })}>
        {props.dishes.map((dish) => (
          <li key={dish.id}>{dish.value}</li>
        ))}
      </ul>
    </section>
  );
}
function Footer(props) {
  return (
    <footer className="App-header">
      <p>Copyright {props.year}</p>
    </footer>
  );
}
const dishes = ["maceroni", "pizza", "Orange Juice"];

const dishObjects = dishes.map((dish, i) => ({ id: i, value: dish }));

export function App() {
  //here we are rendering!!
  return (
    <div className="App">
      <Home />
      <Title
        title="Hello this is a new title!"
        subtitle="My name is musarrat!"
      />
      <Modal>
        <p>This is a children prop</p>
      </Modal>
      <Modal>
        <img src={logo} alt="" />
      </Modal>
      <TripList />
      <MyTodoList />
      <Name />
      <Header />
      <Welcome />
      <Message name="Mush" />
      <Namelist />
      <Form />
      <Main adjective="amazing" dishes={dishObjects} />
      <Footer year={new Date().getFullYear()} />
    </div>
  );
}

export default App;
