import React, { Component } from "react";
// import logo from "./logo.svg";
import "./App.css";
import Greet from "./myComponents/greet";
import Welcome from "./myComponents/welcome";
import Message from "./myComponents/message";
import Scene from "./myComponents/scene";
import Form from "./components/form";
import PureComp from "./myComponents/PureComp";

class App3 extends Component {
  render() {
    return (
      <div className="App-header">
        <Greet name="Tanvir" heroname="Batman" />
        <Greet name="Sami" heroname="Superman" />
        <Greet name="Vumi" heroname="Jharuman" />
        <Message name="musarrat" addValue={5} />
        <PureComp />
        <Scene />
        <Form />
        <Welcome />
      </div>
    );
  }
}
export default App3;
