import React from "react";
import { Component } from "react";

class Scene extends Component {
  constructor() {
    super();
    this.state = {
      fontColor: "black",
      color: "#454554",
      value: 44,
      text: "This is not an image!",
    };
  }
  changeState() {
    this.setState({
      color: "#fc9999",
      value: this.state.value + 1,
    });
  }

  render() {
    return (
      <div style={{ backgroundColor: this.state.color }}>
        <h3 style={{ color: this.state.fontColor }}>{this.state.text}</h3>
        <h4>{this.state.value}</h4>
        <button
          className="button"
          onClick={() => {
            this.changeState();
          }}
          onMouseMove={() => {
            this.setState({
              fontColor: "white",
              text: "Now this is a image.",
            });
          }}
        >
          Change COlor
        </button>
      </div>
    );
  }
}

export default Scene;
