import "./App.css";
// import { useFetch } from "./hooks/useFetch";
// import from "./nodelogo.jpg";
import { React, useState } from "react";

function Header() {
  const [name, changeName] = useState("Mehjabin");
  const [color, changeColor] = useState("tomato");
  return (
    <header className="App-header" style={{ color: `${color}` }}>
      <h3>Name is {name}</h3>
      <button
        onClick={() => {
          changeName("Musarraty");
          changeColor("darkblue");
        }}
      >
        Change
      </button>
    </header>
  );
}

let style = {
  color: "#f00",
  fontFamily: "cursive",
};

function Section(props) {
  return (
    <section className="App-header">
      <p>Some words</p>
      <ul style={style}>
        {props.objs.map((obj) => {
          return <li key={obj.id}>{obj.name}</li>;
        })}
      </ul>
    </section>
  );
}

function Footer(props) {
  return (
    <footer className="App-header">
      <p>Today is {props.date} </p>
    </footer>
  );
}

let objects = ["mental", "mimic", "terrible"];
let objCollection = objects.map((obj, i) => {
  return { id: i, name: obj };
});
console.log(objCollection);
export function App2() {
  return (
    <div className="App">
      <Header />
      <Section amount="900" objs={objCollection} />
      <Footer
        date={`0${new Date().getDate()}/0${new Date().getMonth()}/${new Date().getFullYear()}`}
      />
    </div>
  );
}
export default App2;
